<template>
	<div class="theme-dashboard-view">
		<h1>Drivers</h1>

		<!-- Loading Info -->
		<div class="d-flex justify-content-center">
			<LoadingIndicator :active="loading" />
		</div>
		<div v-show="load_error">
			<div class="justify-content-center text-center text-danger my-4">
				<h4>{{ load_error }}</h4>
			</div>
		</div>
		<div class="my-4" v-show="!loading">
			<Table
				:rows="drivers_data"
				:contain="[
					'id',
					'firstName',
					'lastName',
					'phoneNumber',
					'dateOfBirth',
				]"
			>
				<template v-slot:id-title> ID </template>

				<template v-slot:firstName-title> First Name </template>

				<template v-slot:lastName-title> Last Name </template>

				<template v-slot:phoneNumber-title> Phone Number </template>
				<template v-slot:dateOfBirth-title> Date Of Birth </template>
			</Table>
			<div class="d-flex justify-content-center">
				<PageNav
					:pages="pages"
					:current_page="current_page"
					@click-page="getDrivers"
					@next-page="nextPage"
					@prev-page="prevPage"
					@goto-first="gotoFirstPage"
					@goto-last="gotoLastPage"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import Table from "@/components/Table.vue";
import LoadingIndicator from "@/components/LoadingIndicator.vue";
import PageNav from "@/components/PageNav.vue";

// Services
import DriverService from "@/services/drivers.js";

export default {
	name: "Drivers",
	components: {
		Table,
		LoadingIndicator,
		PageNav,
	},
	data() {
		return {
			loading: false,
			load_error: "",
			drivers_data: [],

			current_page: 1,
			pages: 1,
		};
	},
	methods: {
		// Page Navigation
		nextPage() {
			if (this.current_page != this.pages) {
				this.getDrivers(this.current_page + 1);
			}
		},
		prevPage() {
			if (this.current_page != 1) {
				this.getDrivers(this.current_page - 1);
			}
		},
		gotoFirstPage() {
			if (this.current_page != 1) {
				this.getDrivers(1);
			}
		},
		gotoLastPage() {
			if (this.current_page != this.pages) {
				this.getDrivers(this.pages);
			}
		},

		getDrivers(page) {
			this.loading = true;
			this.load_error = "";
			DriverService.getDrivers(page).then(
				(response) => {
					this.loading = false;
					this.current_page = page;
					console.log(response);
					this.drivers_data = response.data.data.drivers;
					this.pages = response.data.data.total_pages;
				},
				(error) => {
					this.loading = false;
					console.log("FAIL");
					this.load_error =
						(error.response &&
							error.response.data &&
							error.response.data.message) ||
						error.message ||
						error.toString();
				}
			);
		},
	},
	mounted() {
		this.getDrivers(1);
	},
};
</script>